 // Importa o Conversation do pacote @11labs/client
 import { Conversation } from '@11labs/client';

 const startBtn = document.getElementById('startBtn');
 const stopBtn = document.getElementById('stopBtn');
 const statusEl = document.getElementById('status');
 let conversationInstance = null;
 let audio = null;

// on page load: check for name and id parameters in url, then set the <span id=name> object


const urlParams = new URLSearchParams(window.location.search);
let config = {
    name: urlParams.get('name'),
    agentId: urlParams.get('id'),
}

//try to load  a {name}.json from the server overwriting the whole config object

if (!config.agentId) {
  fetch(`agents/${config.name}.json`)
      .then(response => response.json())
      .then(data => {
          config = data;
          console.log('Config loaded:', config);
      })
      .catch(error => {
          console.error('Error loading config:', error);
      });
  }

document.getElementById('name').textContent = config.name;

if (!config.agentId) {
    document.getElementById('startBtn').disabled = true;
    document.getElementById('startBtn').innerText = "Agente no encontrado!";
}

 // Funo para atualizar o status na interface
 const updateStatus = (status) => {
   statusEl.textContent = "Status: " + status;
 };

 // Funo para iniciar a conversa
 async function startConversation() {
   try {
     // Primeiro, solicite acesso ao microfone e explique o porqu ao usurio
     await navigator.mediaDevices.getUserMedia({ audio: true });
     updateStatus('Microfone liberado');

    //Play an mp3 file and wait for it to finish
      if (config.startAudio) {
        audio = new Audio(config.startAudio);
        audio.play();
        await new Promise((resolve) => {
          audio.onended = resolve;
        });  
      }
      
     // Inicia a sesso de conversa com o agente
     //Parse o ID do agente a partir da URL, parametro id
     conversationInstance = await Conversation.startSession({
       agentId: agentId,
       // Callbacks opcionais:
       onConnect: () => {
         console.log('Conectado ao agente!');
         updateStatus('Conectado');
       },
       onDisconnect: () => {
         console.log('Conexo encerrada.');
         updateStatus('Desconectado');
       },
       onMessage: (message) => {
         console.log('Mensagem recebida:', message);
         // Aqui voc pode, por exemplo, atualizar a interface com transcries ou processar o udio recebido.
       },
       onError: (error) => {
         console.error('Erro na sesso:', error);
         updateStatus('Erro');
       },
       onStatusChange: (status) => {
         console.log('Status alterado:', status);
       },
       onModeChange: (mode) => {
         console.log('Modo alterado:', mode);
       },
     });

     // Atualiza os botes na UI
     startBtn.classList.add('hidden');
     stopBtn.classList.remove('hidden');

   } catch (error) {
     console.error('Erro ao iniciar a conversa:', error);
     updateStatus('Erro ao iniciar');
   }
 }

 // Funo para encerrar a conversa
 async function endConversation() {
   if (conversationInstance) {
     await conversationInstance.endSession();
     conversationInstance = null;
   }
   updateStatus('Desconectado');
   startBtn.classList.remove('hidden');
   stopBtn.classList.add('hidden');
 }

 // Eventos dos botes
 startBtn.addEventListener('click', startConversation);
 stopBtn.addEventListener('click', endConversation);